<template>
	<li class="list-item">
		<a href="javascript:void(0)">
			<div class="pd-thumb">
				<div class="pictures">
					<img :src="item.src" class="thumb-img thumb-img-lazy" />
				</div>
				<div class="thumb-layer"></div>
				<div class="play-btn"></div>
			</div>
			<div class="thumb-info">
				<div class="thumb-list">
					<div class="title">
						{{item.title}}
					</div>
					<div class="thumb-list-info">
						<div class="nickname">{{item.name}}</div>
						<div class="thumb-number">{{item.num}}</div>
					</div>
					<div class="thumb-bar"></div>
				</div>
			</div>
		</a>
	</li>
</template>

<script>
	export default {
		props: ['item'],
	}
</script>

<style>

</style>


